<template>
  <div class="app-container">

  

      <!-- <el-container>
        <el-header>
          <el-input placeholder="Filter keyword" v-model="filterText" style="margin-bottom:30px;"></el-input>
        </el-header>
      </el-container> -->
      <el-container direction="horizontal">
        <el-container direction="vertical"  style="height:700px;width:30%;">
          <el-header>
            <el-row :gutter="10" type="flex">
              <el-col>
                <el-input placeholder="输入要查询的玩家名字" v-model="filterText" style="width:200px;"></el-input>
              </el-col>
              <el-col>
                <el-button type="primary" >确定</el-button>
              </el-col>
            </el-row> 
          </el-header>
          <el-aside>
            <el-tree  class="filter-tree" :data="data2" :props="defaultProps" @node-click="handleNodeClick" style="height:600px"></el-tree>
          </el-aside>
        </el-container>
        <el-container direction="vertical" style="height:700px;width:70%"> 
          <el-header>
              <el-input placeholder="Filter keyword" v-model="filterText" style="margin-bottom:30px;width:300px;"></el-input>
          </el-header>  
          <el-main>      
            <el-table
              :data="tableData"
              border
              stripe
              height="600px"
              style="width: 100%">
              <el-table-column 
                v-for="col in cols"
                :key="col.label"
                :prop="col.prop" :label="col.label" >
              </el-table-column>
              <!-- <el-table-column
                prop="date"
                label="日期"
                width="180">
              </el-table-column> -->
            </el-table>
          </el-main>
        </el-container>

      </el-container>

  </div>

 
</template>

<script>
import { playerInfo } from '@/api/player';
export default {
  watch: {
    filterText: function(val, oldVal) {
      console.log(val);
      this.tableData = this.tableData.filter(p => ~p.Key.indexOf(val));
    }
  },
  methods: {
    handleNodeClick(data) {
      this.tableData = [];
      this.cols = [];
      console.log(data);
      // 属性
      if (data.type === 0) {
        this.tableData = data.data;
        this.cols.push(
          { prop: 'Key', label: '属性' },
          { prop: 'Val', label: '值' }
        );
        console.log(this.cols);
      }
      // 表格
      if (data.type === 1) {
        var cols_arr = data.data.Cols.ColType;
        var rows_arr = data.data.Rows;
        var table_data = [];
        for (let col_index = 0; col_index < cols_arr.length; col_index++) {
          var temp_key = 'Key' + col_index.toString();
          table_data[temp_key] = [];

          this.cols.push({
            prop: temp_key,
            label: cols_arr[col_index].toString()
          });
        }

        for (let row_index = 0; row_index < rows_arr.length; row_index++) {
          const element = rows_arr[row_index].Values;
          var every_arr = {};
          for (let a = 0; a < cols_arr.length; a++) {
            var temp2_key = 'Key' + a.toString();
            every_arr[temp2_key] = element[a].Val;
          }
          table_data.push(every_arr);
        }

        this.tableData = table_data;
      }
      this.filter_data = this.tableData;
    }
  },

  created() {
    const data = { game_id: 1, server_id: 10, role_name: '高村梦火' };
    playerInfo(data).then(resposne => {
      this.data2 = resposne.data;
      console.log(resposne);
    });
  },
  data() {
    return {
      // filter_data,
      tableData: [],
      cols: [],
      data2: [],
      filterText: '',
      defaultProps: {
        children: 'children',
        label: 'label'
      }
    };
  }
};
</script>




